import {usersAPI} from "../api/api";
import {updateObjInArray} from "../utils/object-helpers";

const FOLLOW = 'FOLLOW'
const UNFOLLOW = 'UNFOLLOW'
const SET_USERS = 'SET_USERS'
const SET_CURRENT_PAGE = 'SET_CURRENT_PAGE'
const SET_CURRENT_PORTION = 'SET_CURRENT_PORTION'
const SET_TOTAL_USERS_COUNT = 'SET_TOTAL_USERS_COUNT'
const TOGGLE_IS_FETCHING = 'TOGGLE_IS_FETCHING'
const TOGGLE_IS_FOLLOWING_PROGRESS = 'TOGGLE_IS_FOLLOWING_PROGRESS'

let initialState = {
    users: [],
    pageSize: 10,
    totalUsersCount: 0,
    currentPage: 1,
    currentPortion: 1,
    isFetching: true,
    followingInProgress: []
}

const usersReducer = (state = initialState, action) => {
    switch (action.type) {
        case FOLLOW:
            return {
                ...state,
                users: updateObjInArray(state.users, action.userId, 'id', {followed: true})
            }
        case UNFOLLOW:
            return {
                ...state,
                users: updateObjInArray(state.users, action.userId, 'id', {followed: false})
            }
        case SET_USERS:
            return {
                ...state,
                users: action.users
            }
        case SET_CURRENT_PAGE:
            return {
                ...state,
                currentPage: action.page
            }
        case SET_CURRENT_PORTION:
            return {
                ...state,
                currentPortion: action.portion
            }
        case SET_TOTAL_USERS_COUNT:
            return {
                ...state,
                totalUsersCount: action.count
            }
        case TOGGLE_IS_FETCHING:
            return {
                ...state,
                isFetching: action.isFetching
            }
        case TOGGLE_IS_FOLLOWING_PROGRESS:
            return {
                ...state,
                followingInProgress: action.isFetching
                    ? [...state.followingInProgress, action.userId]
                    : state.followingInProgress.filter(id => id !== action.userId)
            }
        default:
            return state
    }
}

export const followDone = userId => ({type: FOLLOW, userId: userId})
export const unfollowDone = userId => ({type: UNFOLLOW, userId: userId})
export const setUsers = users => ({type: SET_USERS, users: users})
export const setCurrentPage = page => ({type: SET_CURRENT_PAGE, page: page})
export const setCurrentPortion = portion => ({type: SET_CURRENT_PORTION, portion: portion})
export const setTotalUsersCount = count => ({type: SET_TOTAL_USERS_COUNT, count: count})
export const toggleIsFetching = toggle => ({type: TOGGLE_IS_FETCHING, isFetching: toggle})
export const toggleIsFollowingProgress = (toggle, userId) => ({
    type: TOGGLE_IS_FOLLOWING_PROGRESS,
    isFetching: toggle,
    userId: userId
})


export const requestUsers = (currentPage, pageSize) => async dispatch => {
    dispatch(toggleIsFetching(true))
    dispatch(setCurrentPage(currentPage))
    // dispatch(setCurrentPortion(currentPortion))
    const data = await usersAPI.getUsers(currentPage, pageSize)
    dispatch(toggleIsFetching(false))
    dispatch(setUsers(data.items))
    dispatch(setTotalUsersCount(data.totalCount))
    // dispatch(setTotalUsersCount(88))
}

const toggleFollowUnfollow = async (dispatch, userId, apiMethod, actionCreator) => {
    dispatch(toggleIsFollowingProgress(true, userId))
    const res = await apiMethod(userId)
    if (res.resultCode === 0) {
        dispatch(actionCreator(userId))
    }
    dispatch(toggleIsFollowingProgress(false, userId))
}

export const follow = userId => async dispatch => {
    await toggleFollowUnfollow(dispatch, userId, usersAPI.follow.bind(usersAPI), followDone)
}

export const unfollow = userId => async dispatch => {
    await toggleFollowUnfollow(dispatch, userId, usersAPI.unfollow.bind(usersAPI), unfollowDone)
}


export default usersReducer