import {authAPI, securityAPI} from "../api/api";
import {stopSubmit} from "redux-form";

const SET_AUTH_USER_DATA = 'SET_AUTH_USER_DATA'
const GET_CAPTCHA_URL_DONE = 'GET_CAPTCHA_URL'


let initialState = {
    userId: null,
    email: null,
    login: null,
    isAuth: false,
    captcha: null
}


const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_AUTH_USER_DATA:
        case GET_CAPTCHA_URL_DONE:
            return {
                ...state,
                ...action.payload,
            }
        default:
            return state
    }
}

export const setAuthUserData = (userId, email, login, isAuth) => ({
    type: SET_AUTH_USER_DATA, payload: {
        userId,
        email,
        login,
        isAuth
    }
})

export const getCaptchaUrlDone = captcha => ({
    type: GET_CAPTCHA_URL_DONE, payload: {captcha}
})


export const authorize = () => async dispatch => {
    const res = await authAPI.authorize()
    if (res.data.resultCode === 0) {
        let {id, email, login} = res.data.data
        dispatch(setAuthUserData(id, email, login, true))
    }
}


export const login = (email, password, remember, captcha) => async dispatch => {
    const res = await authAPI.login(email, password, remember, captcha)
    if (res.data.resultCode === 0) {
        dispatch(authorize())
    } else {
        if (res.data.resultCode === 10) {
            dispatch(getCaptchaUrl())
        }
        let error = res.data.messages.length > 0 ? res.data.messages[0] : 'Something is wrong!'
        dispatch(stopSubmit('login', {_error: error}))
    }
}


export const getCaptchaUrl = () => async dispatch => {
    const res = await securityAPI.getCaptcha()
    const captcha = res.data.url
    dispatch(getCaptchaUrlDone(captcha))
}


export const logout = () => async dispatch => {
    const res = await authAPI.logout()
    if (res.data.resultCode === 0) {
        dispatch(setAuthUserData(null, null, null, false))
    }
}


export default authReducer