const SEND_MESSAGE = 'SEND-MESSAGE'


let initialState = {
    dialogs: [
        {
            id: 1,
            name: 'Henry'
        },
        {
            id: 2,
            name: 'Mark'
        },
        {
            id: 3,
            name: 'Clark'
        },
        {
            id: 4,
            name: 'Bruce'
        }
    ],
    messages: [
        {
            id: 1,
            text: 'Good evening!'
        },
        {
            id: 2,
            text: 'You lost your wallet'
        },
        {
            id: 3,
            text: 'How can I return it?'
        },
        {
            id: 4,
            text: 'Ok!'
        }
    ]
}

const dialogsReducer = (state = initialState, action) => {
    switch (action.type) {
        case SEND_MESSAGE:
            let body = action.newMessageBody
            return {
                ...state,
                messages: [...state.messages, {id: 5, text: body}]
            }
        default:
            return state
    }
}


export const sendMessageActionCreator = newMessageBody => ({type: SEND_MESSAGE, newMessageBody: newMessageBody})


export default dialogsReducer